*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  }

ul{
  list-style-type: none;
}

header{
  width: 100%;
  /* position: relative; */
  height: 50px;
   /* background-color: #fff; */
 
}
header section{
  width: 100%;
  max-width: 1300px;
  margin: auto;
  
   /* background-color: #fff; */
  
  /* position: relative; */
  /* left: auto; */
}
.header{
  position: fixed;
  width: 100%;
  max-width: 1300px;
  padding: 20px;
  margin: auto;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  background-color: #fff;
  top: 0;
  z-index: 999;
}
header h3{
  font-size: 2rem;
}

header ul{
  display: flex;
  align-items: center;
  font-size: 2rem;
  gap: 20px;
  
}
header ul li:hover{
  cursor: pointer;
  opacity: 0.8;
}

main{
  width: 100%;
  max-width: 1300px;
  margin: 20px auto;
  padding: 20px;
  display: grid;
  grid-template-columns: 0.8fr 3fr;
}
.nav{
  position: relative;
}
.nav>:first-child{
  position: fixed;
  top: 80px;
  bottom: 0;
  overflow-y: scroll;
  /* background-color: #000; */
  
}
nav::-webkit-scrollbar {
  width: 3px;
}

/* Track */
nav::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
nav::-webkit-scrollbar-thumb {
  background: #c9c5c5; 
}

/* Handle on hover */
nav::-webkit-scrollbar-thumb:hover {
  background: #f2f2f2; 
}
nav{
  /* overflow-y: scroll; */
  /* padding: 0 50px; */
  /* padding-left: 50px; */
  display: flex;
  flex-direction: column;
  gap: 20px;
}
nav ul li{
  padding: 10px 50px 10px 30px;
  /* padding-right: 50px; */
  font-size: 1.1rem;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  gap: 10px;

}
nav ul li svg{
  font-size: 1.2rem;
}
nav ul li:hover{
  background-color: #f1f1f1;
  cursor: pointer;
}
nav ul>:first-child{
  opacity: 0.5;
  font-size: 1.5rem;
  font-weight: 500;
  padding: 15px 0;

}
nav ul>:first-child:hover{
  cursor: default;
  background-color: #fff;
}

.centre{
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 20px;
  border-radius: 10px;
  justify-content: flex-start;
  align-items: flex-start;
  background-color:rgb(238, 242, 246);
}


.topCards{
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;
}
.topCards>:first-child{
  width: 70%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}



.card{
  width: 100%;
  padding: 20px;
  box-shadow: 0px 0px 5px 1px #d1cece;
  border-radius: 10px;
  transition: all 0.3s ease;
  background-color: #fff;
  position: relative;
  overflow: hidden;
}

.topCards>:first-child>:nth-child(odd)::before{
content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: -100%;
  background-color: rgb(94, 53, 177);
  transform: translateX(0);
  /* visibility: hidden; */
  transition: all 0.5s ease;
  
  
}
.topCards>:first-child>:nth-child(even)::before{
content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: -100%;
  background-color: rgb(30, 136, 229);
  /* border-radius: 50%; */
  transform: translateX(0);
  /* visibility: hidden; */
  transition: all 0.5s ease;
  
}
.card h4{
  font-weight: 600;
  font-size: 1.3rem;
  padding: 10px 0;
  display: flex;
  align-items: center;
  gap: 10px;
  /* color: rgb(30, 136, 229); */
}
.card div{
  display: flex;
  align-items: center;
  gap: 10px;
}
.card div>:first-child{
  font-size: 1.2rem;
  padding: 10px 0;
  font-weight: bold;
}
.card>:last-child{
  font-size: 0.8rem;
  opacity: 0.8;
}
.card div svg{
  color: red;
  font-weight: bold;
}
.topCards>:first-child>:nth-child(3) div svg{
  color: rgb(30, 136, 229);

}
.topCards>:first-child>:nth-child(odd):hover::before{
  transform: translateX(100%);
}
.topCards>:first-child>:nth-child(even):hover::before{
  transform: translateX(100%);
}


.card:hover,.card:hover h4>:first-child,.card:hover h4 span,.card:hover p{
  z-index: 9999;
  color: #fff !important;
  cursor: pointer;
}

.total{
  width: 30%;
  display: grid;
  grid-template-columns:1fr;
  justify-content: center;
  align-items: flex-start;
  gap: 12px;
}
.total article{

  padding: 10px;
  /* width: 100%; */
  border-radius: 10px;
  color: #fff;
}
.total article h4{
  font-weight: 600;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 10px;
  /* padding: 10px 0; */
  opacity: 0.5;
}
.total article p{
  padding-left: 30px;
  font-weight: 600;
}
.total>:first-child{
  background-color: rgb(94, 53, 177);
}
.total>:nth-child(2){
  background-color: rgb(30, 136, 229);
}


.grid{
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 20px;
  overflow: hidden;
}

.chart{
  width: 100%;
  height: 100%;
   overflow: hidden;
   
}
.chart>:first-child{
  width: 100%;
  height: 100%;
  margin: auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 20px;
 
}
.transactions{
  padding: 20px;
  
  border-radius: 10px;
  background-color: #fff;
  /* position: relative;
  overflow: hidden; */
}
.transactions>:first-child{
  font-size: 1.3rem;
  margin-bottom: 20px;
  opacity: 0.7;
  font-weight: 600;
  padding-bottom: 20px;
  border-bottom: 2px solid rgb(94, 53, 177);
}
.acont{
  position: relative;
  overflow: hidden;
  /* width: 100%;
  height: 100%; */
}
.transactions article{
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
  text-align: center;
  overflow: hidden;
  transition: all 0.3s ease;
  /* border-bottom: 1px solid rgb(30, 136, 229); */
  /* box-shadow: 0px 0px 5px 1px #d1cece; */
}


.transactions article>:first-child{
  font-size: 1.5rem;
  font-weight: 600;
  opacity: 0.7;
  grid-row: 1/3;
}

.transactions article h4{
  font-size: 1rem;
  opacity: 0.8;
  text-transform: uppercase;
  font-weight: 600;
}
.transactions article p{
  font-size: 0.8rem;
  opacity: 0.8;
  /* margin-bottom: 10px; */
}
.transactions article>:nth-child(4){
  margin-bottom: 10px;
}
.underline{
  width: 80%;
  /* padding-bottom: 0; */
  margin: auto;
  /* margin-bottom: 0; */
  height: 1px;
  background-color: rgb(94, 53, 177);
  grid-column: 1/4;
}


.transactions article::before{
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: -100%;
  background-color: rgb(30, 136, 229);
  /* border-radius: 50%; */
  transform: translateX(0);
  /* visibility: hidden; */
  transition: all 0.5s ease;
  
  /* overflow: hidden; */
}
.transactions article:hover{
  cursor: pointer;
  color: #fff;
}
.transactions article:hover::before{
transform: translateX(100%);
visibility: visible;

}








